<template>
  <div :style='{"color":"#666","padding":"20px 30px 30px","fontSize":"16px"}'>
    <el-form
	  :style='{"border":"1px solid #eee","padding":"30px","boxShadow":"0 0px 0px rgba(64, 158, 255, .3)","fontSize":"inherit","borderRadius":"6px","background":"#fff"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      label-width="150px"
    >  
     <el-row>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yonghu'"  label="" prop="yonghuzhanghao">
          <el-input v-model="ruleForm.yonghuzhanghao" readonly              placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yonghu'"  label="" prop="yonghuxingming">
          <el-input v-model="ruleForm.yonghuxingming"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='yonghu'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="yonghutouxiangUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='yonghu'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie"  placeholder="">
            <el-option
                v-for="(item,index) in yonghuxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yonghu'"  label="" prop="nianling">
          <el-input v-model="ruleForm.nianling"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yonghu'"  label="" prop="lianxifangshi">
          <el-input v-model="ruleForm.lianxifangshi"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yisheng'"  label="" prop="yishenggonghao">
          <el-input v-model="ruleForm.yishenggonghao" readonly              placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yisheng'"  label="" prop="yishengxingming">
          <el-input v-model="ruleForm.yishengxingming"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='yisheng'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="yishengtouxiangUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='yisheng'"  label="" prop="keshi">
          <el-select v-model="ruleForm.keshi" :disabled="true" placeholder="">
            <el-option
                v-for="(item,index) in yishengkeshiOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='yisheng'"  label="" prop="jibie">
          <el-select v-model="ruleForm.jibie" :disabled="true" placeholder="">
            <el-option
                v-for="(item,index) in yishengjibieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yisheng'"  label="" prop="lianxidianhua">
          <el-input v-model="ruleForm.lianxidianhua"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yisheng'"  label="" prop="shanzhangjibing">
          <el-input v-model="ruleForm.shanzhangjibing"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}'   v-if="flag=='yisheng'"  label="" prop="zhiyelvli">
          <el-input v-model="ruleForm.zhiyelvli"               placeholder="" clearable></el-input>
        </el-form-item>
		<el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='users'" label="" prop="username">
			<el-input v-model="ruleForm.username" placeholder=""></el-input>
		</el-form-item>
		<el-form-item :style='{"color":"inherit","margin":"0 0 20px 0","fontSize":"inherit"}' v-if="flag=='users'" label="" prop="image">
		  <file-upload
		  tip=""
		  action="file/upload"
		  :limit="1"
		  :multiple="false"
		  :fileUrls="ruleForm.image?ruleForm.image:''"
		  @change="usersimageUploadChange"
		  ></file-upload>
		</el-form-item>
		<el-form-item :style='{"width":"100%","padding":"0","margin":"0","fontSize":"inherit","textAlign":"center"}'>
			<el-button class="btn3" :style='{"border":"0","cursor":"pointer","padding":"0 24px","margin":"4px","outline":"none","color":"#fff","borderRadius":"4px","background":"rgba(86,178,198,.8)","clipPath":"polygon(0% 0%, 95% 0, 100% 50%, 95% 100%, 0% 100%)","width":"auto","fontSize":"inherit","height":"40px"}' type="primary" @click="onUpdateHandler">
				<span class="icon iconfont icon-dagou" :style='{"margin":"0 2px","fontSize":"inherit","color":"#fff","height":"40px"}'></span>
				
			</el-button>
		</el-form-item>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
      yonghuxingbieOptions: [],
      yishengkeshiOptions: [],
      yishengjibieOptions: [],
    };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.yonghuxingbieOptions = ",".split(',')
    this.$http({
      url: `option/keshi/keshi`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.yishengkeshiOptions = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.yishengjibieOptions = ",,,".split(',')
  },
  methods: {
    yonghutouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
    yishengtouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
	usersimageUploadChange(fileUrls) {
		this.ruleForm.image = fileUrls;
	},
    onUpdateHandler() {
      if((!this.ruleForm.yonghuzhanghao)&& 'yonghu'==this.flag){
        this.$message.error('');
        return
      }

        if(this.ruleForm.yonghuzhanghao && (this.ruleForm.yonghuzhanghao.length<3) && `yonghu` == this.flag){
            this.$message.error(`3`);
            return
        }

        if(this.ruleForm.yonghuzhanghao && (this.ruleForm.yonghuzhanghao.length>13) && `yonghu` == this.flag){
            this.$message.error(`13`);
            return
        }
      if((!this.ruleForm.mima)&& 'yonghu'==this.flag){
        this.$message.error('');
        return
      }

        if(this.ruleForm.mima && (this.ruleForm.mima.length<3) && `yonghu` == this.flag){
            this.$message.error(`3`);
            return
        }

        if(this.ruleForm.mima && (this.ruleForm.mima.length>13) && `yonghu` == this.flag){
            this.$message.error(`13`);
            return
        }
      if((!this.ruleForm.yonghuxingming)&& 'yonghu'==this.flag){
        this.$message.error('');
        return
      }




        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }




      if( 'yonghu' ==this.flag && this.ruleForm.nianling&&(!isIntNumer(this.ruleForm.nianling))){
       this.$message.error(``);
        return
      }


      if( 'yonghu' ==this.flag && this.ruleForm.lianxifangshi&&(!isMobile(this.ruleForm.lianxifangshi))){
        this.$message.error(``);
        return
      }


      if( 'yonghu' ==this.flag && this.ruleForm.status&&(!isIntNumer(this.ruleForm.status))){
       this.$message.error(``);
        return
      }


      if( 'yonghu' ==this.flag && this.ruleForm.passwordwrongnum&&(!isIntNumer(this.ruleForm.passwordwrongnum))){
       this.$message.error(``);
        return
      }

        if(this.ruleForm.yishenggonghao && (this.ruleForm.yishenggonghao.length<3) && `yisheng` == this.flag){
            this.$message.error(`3`);
            return
        }

        if(this.ruleForm.yishenggonghao && (this.ruleForm.yishenggonghao.length>13) && `yisheng` == this.flag){
            this.$message.error(`13`);
            return
        }

        if(this.ruleForm.mima && (this.ruleForm.mima.length<3) && `yisheng` == this.flag){
            this.$message.error(`3`);
            return
        }

        if(this.ruleForm.mima && (this.ruleForm.mima.length>13) && `yisheng` == this.flag){
            this.$message.error(`13`);
            return
        }




        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }






      if( 'yisheng' ==this.flag && this.ruleForm.lianxidianhua&&(!isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }






      if( 'yisheng' ==this.flag && this.ruleForm.thumbsupnum&&(!isIntNumer(this.ruleForm.thumbsupnum))){
       this.$message.error(``);
        return
      }


      if( 'yisheng' ==this.flag && this.ruleForm.crazilynum&&(!isIntNumer(this.ruleForm.crazilynum))){
       this.$message.error(``);
        return
      }




      if( 'yisheng' ==this.flag && this.ruleForm.clicknum&&(!isIntNumer(this.ruleForm.clicknum))){
       this.$message.error(``);
        return
      }


      if( 'yisheng' ==this.flag && this.ruleForm.status&&(!isIntNumer(this.ruleForm.status))){
       this.$message.error(``);
        return
      }


      if( 'yisheng' ==this.flag && this.ruleForm.passwordwrongnum&&(!isIntNumer(this.ruleForm.passwordwrongnum))){
       this.$message.error(``);
        return
      }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
	this.$message.error(``);
        return	
      }
	  if(this.flag=='users'){
	  	this.ruleForm.image = this.ruleForm.image.replace(new RegExp(this.$base.url,"g"),"")
	  }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
				if(this.flag=='users'){
					this.$storage.set('headportrait',this.ruleForm.image)
				}
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
	.el-date-editor.el-input {
		width: auto;
	}
	
